.main {
	min-height: 100vh;
	background: rgba(89, 193, 115, 0.5); /* fallback for old browsers */
	background: -webkit-linear-gradient(
		to right,
		rgba(89, 193, 115, 0.6),
		rgba(161, 127, 224, 0.6),
		rgba(93, 38, 193, 0.6)
	); /* Chrome 10-25, Safari 5.1-6 */
	background: linear-gradient(
		to right,
		rgba(89, 193, 115, 0.6),
		rgba(161, 127, 224, 0.6),
		rgba(93, 38, 193, 0.6)
	); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}
